.App {
  text-align: center;
  background-color: black;
}

body {
  background-color: black;
  color: #fff;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: black;
  min-height: 20vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #61dafb;
}

h2 {
  color: #61dafb;
}

h3 {
  font-style: italic;
}

.App-link {
  color: #61dafb;
}

.cart {
  margin: auto;
  width: 60%;
  padding: 10px;
  background-color: black;
}

.products {
  background-color: black;
  margin: auto;
  text-align: left;
  width: 60%;
}

.productdata {
  padding: 2px;
  margin: 2px;
  text-align: left;
}

.img {
  max-width: 90%;
}
.header-img {
  max-width: 100%;
}

.confirm {
  text-align: left;
  margin: auto;
  width: 60%;
  padding: 5px;
}

form {
  margin: 10px;
  color: #61dafb;

}

input {
  padding: 5px;
  margin: 5px;
}

button {
  background-color: white;
  color: black;
  padding: 5px;
  margin: 5px;
  border: 1px solid white;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
}

button:hover {
  background-color: black;
  color: #61dafb;
}


